@import (reference) "../../common.less";
@setting-width: 400px;
@setting-title-height: 60px;

.kline {
  height: 100%;
  display: flex;
  flex-direction: row;
  .kline-setting-container {
    height: 100%;
    width: @setting-width;
    background: #2E3038;
    .kline-setting-title-container {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      color: @white;
      font-size: 18px;
      line-height: @setting-title-height;
      text-align: center;
      background: #3F4243;
      padding-left: 20px;
      padding-right: 20px;
      .kline-setting-title-version {
        font-size: 16px;
      }
      .kline-setting-title-link {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding-left: 8px;
        padding-right: 8px;
        width: 72px;
        height: 34px;
        border: solid @white 1px;
        border-radius: 4px;
        color: @white;
        font-size: 16px;
        .kline-setting-title-link-icon {
          font-size: 20px;
        }
        &:hover {
          color: @second-white;
          border-color: @second-white
        }
      }
    }
    .kline-setting-list-container {
      .kline-setting-list-title {
        height: 36px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        font-size: 16px;
        color: @white;
        padding-right: 16px;
        cursor: pointer;
        transition: all .2s;
        .kline-setting-list-title-text {
          display: inline-block;
          padding-left: 16px;
          border-left: solid 4px transparent;
        }
        .kline-setting-list-title-icon {
          font-size: 22px;
          transition: all .3s;
        }
        &:hover {
          background-color: #2A2A2A;
          color: @second-white;
          .kline-setting-list-title-text {
            border-left-color: @yellow;
          }
        }
      }
      .kline-setting-list-title-expand {
        .kline-setting-list-title-text {
          border-left-color: @yellow;
        }
        .kline-setting-list-title-icon {
          transform: rotate(180deg);
        }
      }
      .kline-setting-list {
        padding-left: 20px;
        padding-right: 20px;
        font-size: 14px;
        display: none;
        .kline-setting-list-row {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          height: 40px;
          border-bottom: solid 1px #404040;
          transition: all .5s;
          color: @gary;
          &[datatype="sub"] {
            justify-content: flex-start;
            border-bottom-color: transparent;
            color: @second-white!important;
            &:hover {
              color: @second-white!important;
            }
            .kline-setting-list-row-arrow {
              display: inline-block;
              margin-right: 8px;
              font-style: normal;
              width: 8px;
              height: 8px;
              border-radius: 50%;
              background: @yellow;
            }
          }
          &:hover {
            color: @yellow;
          }
        }
      }

      .kline-setting-list-expand {
        display: block;
        margin-bottom: 10px;
      }
    }
  }

  .kline-chart {
    height: 100%;
    width: calc(100% - @setting-width);
  }
}

@media only screen and (max-width: 768px) {
  .kline {
    .kline-setting-container {
      display: none;
    }
    .kline-chart {
      width: 100%;
    }
  }
}
